.catalog-item{
  padding-top: 1.75em;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 26.25em) 24.75em;
    gap: 3em 1.5em;
  }
  &__col{
    padding-top: 1em;
  }
  &__sticky{
    position: sticky;
    top: 1em;
  }
  &__nav{
    display: flex;
    margin-top: 3em;
  }
}

.product-gallery{
  margin-top: 1em;
  &__img{
    position: relative;
    padding-bottom: 140%;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 95%;
      display: block;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.5em;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
        gap: .5em;
        transform: unset !important;
      }
      &-slide{
        padding-right: 0;
        &-active{
          grid-column: 1/-1;
        }
        &:not(.swiper-slide-active){
          .product-gallery__img{
            padding-bottom: 100%;
            img{
              max-height: 80%;
            }
          }
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.swiper-pagination{
  display: flex;
  justify-content: center;
  &-bullet{
    background-color: transparent;
    opacity: 1;
    border: 1px solid $text-base-m;
    border-radius: 0;
    width: .75em;
    height: .75em;
    &-active{
      background-color: $text-base-m;
    }
  }
}

.product-nav{
  display: flex;
  margin-top: 3.25em;
  &__link{
    display: flex;
    padding: .5em .75em 1em;
    font-weight: 500;
    position: relative;
    flex-shrink: 0;
    white-space: nowrap;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: .1875em;
      background-color: $text-primary-up;
      transition: all .3s;
      opacity: 0;
    }
    &._active{
      &:before{
        opacity: 1;
      }
    }
    &._active,
    &:hover{
      color: $text-primary-up;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      padding-top: 0;
    }
    &__col2{
      order: -1;
    }
    &__sticky{
      position: static;
    }
  }

  .product-gallery{
    overflow: hidden;
    width: calc(100% + 3.5em);
    margin-left: -1.75em;
    position: relative;
    &__img{
      padding-bottom: 145%;
    }
    &__nav{
      display: none;
    }
    .swiper{
      &-pagination{
        bottom: .5em;
      }
    }
    @media screen and (min-width: 577px){
      padding-bottom: 2em;
      &__img{
        padding-bottom: 75%;
      }
      .swiper-pagination{

      }
    }
  }

  .product-nav{
    @include fullScroll;
    margin-top: 3em;
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    .prop-item{
      grid-template-columns: 12em calc(100% - 12.5em);
    }
    &__btn{
      width: 100%;
    }
  }
}
