.header-bottom{
  border-top: 1px solid #c1c3cc;
}

.nav{
  .menu{
    display: flex;
    align-items: center;
    &>.menu-item{
      @include lastM(0 3.5em 0 0);
      position: relative;
      padding: .625em 0;
      &-has-children{
        cursor: pointer;
      }
      a{
        cursor: pointer;
      }
      &:hover{
        .sub-menu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
  a{
    font-size: 1.125em;
    line-height: 133%;
  }
  .sub-menu{
    @include grid100gap(1em);
  }
  @media screen and (min-width: 993px){
    .sub-menu{
      display: grid !important;
      position: absolute;
      top: 100%;
      left: 0;
      min-width: 11.25em;
      box-shadow: .125em .5em .75em rgba(46, 44, 47, 0.2);
      background-color: $bg-light;
      padding: .5em 1em;
      border: $borderUp;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &:before{
        content: '';
        position: absolute;
        bottom: 100%;
        left: 0;
        width: 100%;
        height: 2px;
        margin-bottom: -1px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header-bottom{
    transform: translateX(-120%);
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 4.5em 0 0;
    z-index: 10;
    .container{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
      border-top: $borderM;
    }
  }

  .nav{
    width: 100%;
    .menu{
      align-items: flex-start;
      flex-direction: column;
      &>.menu-item {
        width: 100%;
        padding: 1em 1.75em;
        margin: 0 !important;
        &-has-children{
          &>a{
            display: flex;
            align-items: center;
            &:after{
              content: '';
              width: 1.11em;
              height: 1.11em;
              margin-left: .22em;
              background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 6.40002L8 10.4L12 6.40002' stroke='%232E2C2F' stroke-width='1.2'/%3E%3C/svg%3E");
              @include bgImgSize(100%);
              transition: all .3s;
            }
          }
          &._active{
            &>a{
              color: $text-primary-m;
              &:after{
                transform: rotate(-180deg);
                background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 6.40002L8 10.4L12 6.40002' stroke='%23158289' stroke-width='1.2'/%3E%3C/svg%3E");
              }
            }
          }
        }
      }
    }
    .sub-menu{
      padding-left: 2.5em;
      display: none;
      margin: 1em 0 -1em;
      gap: 0;
      li{
        padding: 1em 0;
      }
    }
  }

  .header-profile{
    flex-shrink: 0;
    display: flex;
    align-items: center;
    height: 3.5em;
    padding: 0 1.75em;
    border-top: $borderM;
    width: 100%;
    svg{
      width: 1.25em;
      margin-right: .25em;
    }
  }

  .header-nav2{
    border-top: $borderM;
    width: 100%;
    padding: 1em 1.75em 0;
    li{
      padding: 1em 0;
    }
    a{
      font-size: 1.125em;
      line-height: 133%;
    }
  }

  .header-city2{
    border-top: $borderM;
    width: 100%;
    padding: 1em 1.75em;
    &__item{
      padding: 1em 0;
    }
  }
}


