.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $text-base-m;
    opacity: .48;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 41em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 1.75em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        margin-left: auto;
    }
}

.close-btn{
    width: 1.25em;
    @include center;
    flex-shrink: 0;
    svg{
        width: 100%;
    }
    &:hover{
        path{
            fill: $text-primary-up;
        }
    }
}

.modal-form{
    margin-top: 1.25em;
    @include grid100gap(1em);
    .rating{
        justify-content: flex-end;
    }
    &__nav{
        display: flex;
        justify-content: flex-end;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }

    .modal-form{
        .rating{
            justify-content: unset;
        }
        &__btn{
            width: 100%;
        }
    }
}
