.text{
  p{
    @include lastM(0 0 .5em);
  }
  li,
  p{
    font-size: 1.125em;
    line-height: 133%;
  }
  ul{
    @include grid100gap(.5em);
    padding-left: .625em;
    li{
      position: relative;
      padding-left: 1em;
      &:before{
        content: '•';
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}


