.filter-item{
  position: relative;
  &._open{
    .filter-item__show{
      border-color: $icon-primary-m;
      background-color: $text-on-dark-primary-dn;
      &:after{
        transform: rotate(-180deg);
      }
    }
    .filter-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._show-count{
    .filter-item__count{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2.5em;
    border: $border;
    padding: 0 .5em;
    transition: all .3s;
    &:after{
      content: '';
      width: 1em;
      height: 1em;
      flex-shrink: 0;
      background-image: url(../img/arrow-down.svg);
      @include bgImgSize(100%);
      margin-left: .25em;
      transition: inherit;
    }
    &:hover{
      cursor: pointer;
      border-color: $icon-primary-m;
      background-color: $text-on-dark-primary-dn;
    }
  }
  &__count{
    padding: 0 .25em;
    height: 1.25em;
    @include center;
    min-width: 1.5em;
    background-color: $icon-primary-dn;
    margin-left: auto;
    color: $text-on-dark-base-m;
    opacity: 0;
    pointer-events: none;
  }
  &__hidden{
    position: absolute;
    margin-top: 1em;
    min-width: 100%;
    box-shadow: $dr-shadow;
    background-color: $bg-light;
    padding: 1.25em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__title{
    margin-bottom: 1em;
  }
  &__options{
    @include grid100gap(1em);
  }
}

.filter-option{
  display: flex;
  align-items: center;
  min-height: 1.5em;
  &__icon{
    border: 1px solid $line-base-dn;
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    margin-right: .5em;
    background-image: url(../img/check.svg);
    @include bgImgSize(0);
  }
  &__count{
    color: $text-base-up;
    margin-left: auto;
  }
  input:checked{
    &~.filter-option__icon{
      background-size: .875em auto;
      border-color: $icon-primary-dn;
      background-color: $back-primary-dn;
    }
  }
}

.props{
  margin-top: 3em;
  @include grid100gap(1em);
}

.prop-item{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .25em));
  gap: .5em;
  color: $text-base-up;
  &__desc{
    color: $text-base-dn;
  }
}

.catalog-delivery{
  @include grid100gap(.5em);
  margin-top: 3em;
  padding-left: .75em;
  &__item{
    max-width: 22.5em;
    display: flex;
    align-items: flex-start;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
}


@media screen and (min-width: 993px){
  ._hide-desktop{
    display: none;
  }
}


@media screen and (max-width: 992px){
  ._hide-tablet{
    display: none;
  }

  .catalog-delivery{
    padding-left: 0;
    &__item{
      max-width: 100%;
    }
  }
}


