.footer{
  background-color: $text-base-m;
  color: #fafafd;
  &__logo{
    display: block;
    width: 21.25em;
    max-width: 100%;
    margin: 0 auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    margin-right: .57em;
  }
  &__conf{
    margin-left: auto;
  }
}

.footer-top{
  padding: 3em 0;
  &__inner{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .625em));
    gap: .9375em;
  }
  &__col3{
    padding-left: 3.25em;
  }
}

.footer-nav{
  margin-top: .5em;
  ul{
    @include grid100gap(.5em);
  }
  a{
    font-size: 1.125em;
    line-height: 133%;
  }
}

.footer-brands{
  margin-top: 3.5em;
  &__items{
    margin-top: .5em;
    display: flex;
    align-items: center;
  }
  &__item{
    margin-right: 1.75em;
    height: 2.25em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
    &:not(:hover){
      opacity: .6;
    }
  }
}

.footer-contacts{
  &__item{
    margin-top: .5em;
  }
}

.footer-contacts2{
  margin-top: 5.75em;
}

.footer-bottom{
  padding: 1em 0;
  border-top: 1px solid $line-base-m;
  &__inner{
    display: flex;
    align-items: center;
  }
}


@media screen and (max-width: 992px){
  .footer-top{
    padding: 3em 0;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .875em));
      gap: 1.75em;
    }
    &__col2{
      order: -1;
      grid-column: 1/-1;
    }
    &__col3{
      padding: 0;
    }
  }

  .footer-brands{
    padding: 1.75em 0 1em;
    margin-top: 0;
  }

  .footer-contacts2{
    margin-top: 1.75em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    text-align: center;
    .container{
      padding: 0 1em;
    }
    &__c{
      margin: 0 0 .57em;
    }
    &__conf{
      margin: .57em 0 0;
    }
  }

  .footer-top{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .footer-brands{
    &__items{
      justify-content: center;
    }
    &__item{
      margin: 0 .875em;
    }
  }

  .footer-bottom{
    padding-bottom: 1.75em;
    &__inner{
      flex-direction: column;
    }
  }
}
