.breadcrumbs {
    margin-bottom: 1.5em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: $text-base-up;
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .3125em;
                right: 100%;
                margin-right: .25em;
                width: .75em;
                height: .75em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
            }
        }
        &:last-child{
            a{
                color: $text-primary-m;
            }
        }
    }
    a{
        font-size: .875em;
        line-height: 114%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
