.brand{
  background: #c3d6de;
  margin: 3.25em 0 2.5em;
  overflow: hidden;
  &__inner{
    padding: 4.25em 0;
    min-height: 28em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 37.875em;
      height: 37.875em;
      border-radius: 50%;
      background: rgba(71, 91, 99, 0.6);
      filter: blur(500px);
      top: -5.125em;
      left: 23em;
    }
  }
  &__content{
    max-width: 27.5em;
    position: relative;
    z-index: 2;
  }
  .text{
    margin-top: 1.75em;
  }
  &__img{
    position: absolute;
    left: 18.75em;
    bottom: 0;
    width: 39em;
    z-index: 3;
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-section{
  padding: 2.5em 0;
}

.collection{
  overflow: hidden;
}

.slider{
  .swiper{
    &-container{
      width: calc(100% + .5em);
    }
    &-slide{
      padding-right: .5em;
    }
  }
}

.collection-slider{
  margin-top: 3em;
  position: relative;
  &:hover{
    .swiper-button{
      opacity: 1;
    }
  }
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-slide{
      width: 25%;
    }
    &-button{
      top: 10.375em;
      opacity: 0;
    }
  }
}

.swiper-button{
  width: 2.75em;
  height: 2.75em;
  padding: 0;
  @include center;
  top: 50%;
  transform: translateY(-50%);
  margin: 0;
  &-prev{
    left: 0;
  }
  &-next{
    right: 0;
  }
  svg{
    width: 1.25em;
  }
  &:hover{
    path{
      stroke: $text-base-m;
    }
  }
}

.product-card{
  position: relative;
  .m-badge{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
  }
  &__img{
    background-color: $root-base-up;
    @include imgCover(113%);
  }
  &__content{
    margin-top: 1.25em;
  }
  .catalog-info__prices{
    margin: 0;
  }
  &__info{
    margin-top: .5em;
    @include grid100gap(.25em);
  }
}

.m-badge{
  background-color: $text-error-m;
  height: 2em;
  display: flex;
  align-items: center;
  padding: 0 .75em;
  color: $text-on-dark-base-m;
}


@media screen and (max-width: 992px){
  .brand{
    &__inner{
      &:before{
        display: none;
      }
    }
  }

  .collection{
    .h2{
      text-align: center;
    }
  }

  .collection-slider{
    margin-top: 3em;
    .slider__wrap{
      overflow: unset;
      width: 18.375em;
      margin: 0 auto;
    }
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .brand{
    position: relative;
    &__inner{
      padding: 3em 0 24.25em;
      position: static;
    }
    &__img{
      left: 0;
      width: 100%;
      height: 22.5em;
      img{
        width: 185%;
        margin-left: -25%;
      }
    }
  }
}
