.gallery-modal{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  z-index: 100;
  background-color: #fff;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  .close-btn{
    position: absolute;
    top: 1em;
    right: 1em;
  }
}

.gallery{
  height: 100%;
  &__small{
    width: 10.5em;
    position: absolute;
    top: 0;
    left: 0;
    padding: 1.5em;
    height: $vh !important;
    .swiper-slide{
      height: 8em;
      padding-bottom: .5em;
      &-thumb-active{
        .gallery__img{
          border-color: $text-base-m;
        }
      }
    }
    .gallery__img{
      cursor: pointer;
      border: 1px solid transparent;
      img{
        max-width: 90%;
        max-height: 90%;
      }
    }
  }
  &__big{
    width: 50em;
    max-width: calc(100% - 20em);
    margin: 0 auto;
    height: $vh !important;
    transform: translateX(1.5em);
    .swiper{
      height: 100%;
      &-wrapper{
        align-items: stretch;
      }
      &-slide{

      }
    }
  }
  &__img{
    height: 100%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .gallery-modal{
    display: none;
  }
}


