.product-desc{
  margin-top: 5em;
  .text{
    margin-top: 1em;
  }
}

.product-sizes{
  margin-top: 1.75em;
}

.sizes-table{
  margin-top: 1em;
  color: $text-base-dn;
  table{
    width: 100%;
    border-collapse: collapse;
  }
  tr{
    &:first-child{
      td{
        border-top: 1px solid $line-base-up;
      }
    }
    &:last-child{
      td{
        border-bottom: 1px solid $line-base-up;
      }
    }
  }
  td{
    padding: 0 .75em;
    height: 4.5em;
    &:first-child{
      border-left: 1px solid $line-base-up;
      padding-left: 1.5em;
    }
    &:last-child{
      border-right: 1px solid $line-base-up;
      padding-right: 2em;
    }
  }
}

.product-detail{
  margin-top: 5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 3.125em));
  gap: 3em 4.25em;
  .text{
    margin-top: 1em;
  }
}

.catalog-info{
  &__art{
    margin-top: .5em;
    font-weight: 500;
  }
  &__prices{
    display: flex;
    margin-top: 1.75em;
  }
  &__price2{
    margin-left: .75em;
    color: $text-base-up;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      height: 1.5px;
      width: calc(100% + .25em);
      background-color: $text-base-up;
    }
  }
}

.catalog-color{
  margin-top: 1.875em;
  &__items{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    display: block;
    width: 5em;
    height: 5em;
    margin: .5em .875em 0 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    &._active{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: .25em;
        background-color: $text-primary-up;
      }
    }
  }
}

.size-filter{
  margin-top: 3em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .25em));
  gap: .5em;
  .filter-item{
    &__show{
      background-color: $back-primary-up;
      border-color: $back-primary-up;
    }
    &__hidden{
      width: 14.75em;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__link{
    color: $text-primary-up;
  }
}


@media screen and (max-width: 992px){
  .product-desc,
  .product-sizes{
    margin-top: 3em;
  }

  .product-detail{
    margin-top: 3em;
    grid-template-columns: 100%;
  }

  .catalog-color{
    margin-top: 0;
  }
}


@media screen and (max-width: 576px){
  .sizes-table{
    @include fullScroll;
    table{
      min-width: 48em;
    }
  }

  .size-filter{
    grid-template-columns: 100%;
    gap: 1em;
    &__nav{
      display: flex;
      justify-content: flex-end;
    }
    &__link{
      text-decoration: underline;
      color: $text-primary-m;
      span{
        font-size: 1.125em;
        line-height: 133%;
      }
    }
  }
}
