@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 2.75em;
    font-weight: 500;
    span{
        font-size: 1.125em;
        line-height: 111%;
    }
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $icon-primary-dn;
    color: $text-on-dark-base-m;
    &:hover{
        background-color: $text-on-dark-primary-dn;
        color: $text-base-m;
        ._stroke{
            stroke: $text-base-m;
        }
    }
    &:disabled{
        background-color: $root-base-up;
        color: $text-base-up;
        ._stroke{
            stroke: $text-base-up;
        }
    }
}

.m-btn-primary-outline{
    border: 1px solid $icon-primary-m;
    color: $text-primary-up;
    &:hover{
        background-color: $back-primary-up;
        color: $text-primary-up;
    }
    &:disabled{
        border-color: $line-base-up;
        color: $text-base-up;
    }
}

.text-btn{
    color: $text-primary-m;
    text-decoration: underline;
    span{
        font-size: 1.125em;
        line-height: 133%;
    }
    &:hover{
        color: $text-primary-up;
    }
}
