.header{
  position: relative;
  z-index: 20;
  border-bottom: $borderM;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 13.75em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.header-top,
.header-middle{
  &__inner{
    display: grid;
    grid-template-columns: calc(50% - 12.5em) 25em calc(50% - 12.5em);
    align-items: center;
  }
  &__col1,
  &__col2,
  &__col3{
    display: flex;
    align-items: center;
  }
  &__col2{
    justify-content: center;
  }
  &__col3{
    justify-content: flex-end;
  }
}

.header-top{
  padding: .5em 0;
  background-color: $root-base-up;
  &__by{
    display: flex;
    align-items: center;
    img{
      width: 1.625em;
    }
  }
}

.header-city{
  margin-right: 1.25em;
  &__show{
    display: flex;
    align-items: center;
    color: $text-base-dn;
    transition: all .3s;
    cursor: pointer;
    &:hover{
      color: $text-primary-up;
      path{
        stroke: $text-primary-up;
      }
    }
    svg{
      width: 1.3125em;
      margin-left: .25em;
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.header-contacts{
  &__phone{
    margin-left: .57em;
  }
}

.header-nav{
  .menu{
    display: flex;
    li{
      margin-left: 1.25em;
    }
    a{
      font-size: .875em;
      line-height: 114%;
    }
  }
}

.header-middle{
  padding: 1.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(50% - 7.5em) 15em calc(50% - 7.5em);
    align-items: center;
  }
}

.header-search{
  &__btn{
    display: none;
  }
  &__field{
    width: 24.375em;
    max-width: 100%;
    input{
      padding-left: 2.11em;
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .44em;
  }
}

.header-action{
  &__item{
    display: flex;
    align-items: center;
    margin-left: 1.25em;
    &:hover{
      path{
        fill: $text-primary-m;
      }
    }
  }
  &__icon{
    margin-right: .25em;
    @include center;
    svg{
      width: 1.3125em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0 .5em;
    .container{
      padding: 0;
    }
    &._open{
      .header-bottom{
        transform: translateX(0);
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .header-top{
    display: none;
  }

  .header-search{
    &._show{
      .header-search__btn{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .header-search__field{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__field{
      position: absolute;
      width: calc(100vw - 1.75em);
      left: .875em;
      top: 100%;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
  }

  .header-middle{
    padding: 0;
    position: relative;
    z-index: 12;
    &__inner{
      grid-template-columns: calc(50% - 4.5em) 9em calc(50% - 4.5em);
    }
  }

  .header-search__btn,
  .burger{
    position: relative;
    width: 3em;
    height: 3em;
    display: flex;
    padding: 0;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
    }
  }

  .header-action{
    &__item{
      margin-left: 0;
      .text18{
        display: none;
      }
    }
    &__icon{
      margin: 0;
      width: 3em;
      height: 3em;
      svg{
        width: 1.75em;
      }
    }
  }
}


