.rating {
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.5625em;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: $text-primary-up;
      }
      &~.rating-input__item {
        path {
          fill: $text-primary-up;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: $text-primary-up;
  }
}

.reviews{
  &__nav{
    display: flex;
    margin-top: 1.75em;
  }
  &__items{
    margin-top: 3em;
    @include grid100gap(3em);
    width: 48.25em;
    max-width: 100%;
  }
  &__nav2{
    width: 48.25em;
    max-width: 100%;
    margin-top: 3em;
    display: flex;
    justify-content: center;
  }
  &__btn2{
    svg{
      width: 1.3125em;
    }
  }
}

.review-card{
  display: grid;
  grid-template-columns: 9.375em calc(100% - 9.375em);
  align-items: flex-start;
  &__col2{
    @include grid100gap(.25em);
  }
  &__text{
    margin-top: .25em;
  }
}

.use-slider{
  position: relative;
  margin-top: 3em;
  width: 48.25em;
  max-width: 100%;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-slide{
      width: 16.25em;
    }
  }
}

.use-card{
  cursor: pointer;
  @include imgCover(100%);
}

.use-slider2{
  overflow: hidden;
  margin-top: 1em;
  .use-card{
    pointer-events: none;
  }
}


@media screen and (max-width: 992px){
  .reviews{
    .h2{
      text-align: center;
    }
    &__nav{
      justify-content: center;
    }
  }

  .how-use{
    .h2{
      text-align: center;
    }
    .container{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .review-card{
    grid-template-columns: 100%;
    gap: .75em;
  }
}
